// Import Styled
import styled from 'styled-components';

export const MenuContainer = styled.div`
	padding-top: 16px;
	padding-bottom: 16px;

	@media (min-width: 768px) {	
		padding-top: 40px;
		padding-bottom: 40px;
	}
	&:after{
		content: "";
		display: block;
		clear: both;
	}
	.right-area{
		button, text{
			float: right;
		}
		text{
			color: #fff;
		}
	}
	.notification{
	    position: relative;
	    top: -6px;
	    margin-right: 40px;
	}
	.showMenu{
		display: block;
	}	
	.hideMenu{
		display: none;
	}
`;